.head {
  display: flex;
  justify-content: center;
}

.head .header {
  max-width: 1440px;
  width: 100%;
  background: url("/img/building1080592.png") no-repeat;
  background-size: contain;
  position: relative;
}

.head .header .spacer {
  padding-top: 66.666%;
  width: 100%;
}

.head .header .items {
  top: 0;
  left: 0;
  position: absolute;
  width: 100%;
  height: 100%;
  margin-left: 0;
  margin-right: 0;
  display: flex;
  flex-wrap: wrap;
}

.head .header .items .first {
  width: 100%;
  display: flex;
  height: 45%;
}

.head .header .items .first .black {
  display: flex;
  flex-direction: column;
  justify-content: center;
  background: #4a4a4a;
}

.head .header .items .first .black h2 {
  width: 100%;
  font-weight: 300;
  font-family: Roboto-Light;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  text-align: center;
  color: #ffffff;
}

.head .header .items .first .black .green {
  color: #7ed321;
  font-weight: 900;
  font-family: Roboto-Black;
}

.head .header .items .second {
  width: 100%;
  display: flex;
  height: 55%;
}

.head .header .items .second .black {
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-family: Roboto;
  align-items: center;
  background: #4a4a4a;
}

.head .header .items .second .black h1 {
  font-weight: 300;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
}

.head .header .items .second .black h4 {
  width: 75%;
  color: #ffffff;
  font-weight: 300;
}

.head .header .items .second .black h4 a {
  color: #ffffff;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.67;
  letter-spacing: normal;
  text-align: center;
}

.head .header .items .second .black .green {
  color: #7ed321;
}

.head .header .items .second .black .green a {
  color: #7ed321;
}

.head .header .items .item {
  width: 25%;
  margin: 0 0 0 0;
}

.head .header .items .opacity {
  background: #f2f2f2;
  opacity: 0.5;
}

.main .about_company {
  margin-top: 107px;
  font-size: 36px;
  font-weight: 300;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  font-family: Roboto;
  letter-spacing: normal;
  text-align: left;
  color: #7ed321;
}

.main .about_company span {
  font-weight: 900;
}

.main .items {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  max-height: 620px;
  position: relative;
}

.main .items .images {
  background: black;
  width: 39.3%;
}

.main .items .enclosed {
  position: absolute;
  top: 49%;
  right: 37%;
  flex-basis: 415px;
  border: solid 1px #979797;
  background: black;
}

.main .items .h5 {
  width: 48.1%;
  font-size: 18px;
  padding-left: 18px;
  font-family: Roboto;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  text-align: left;
  color: #000000;
  border-left: solid 2px #7ed321;
  margin-bottom: 12%;
}

.main .items .info {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 33%;
  height: 100px;
  background: #fafafa;
  border: 1px solid #7ed321;
  margin: 0 0 0 auto;
  font-family: Roboto;
  color: #000000;
}

.main .items .info span {
  opacity: 0.8;
  font-size: 40px;
  font-weight: bold;
  margin: 0 3%;
}

.main .items .info h6 {
  opacity: 0.8;
  font-size: 16px;
}

.services {
  background: #fafafa;
  padding-top: 3%;
  margin-top: 5.6%;
}

.services .title {
  text-align: left;
  margin: 0;
}

.services .items .item {
  display: flex;
  margin-top: 5.2%;
}

.services .items .item:first-child {
  margin-top: 4.4%;
}

.services .items .item:hover .img {
  opacity: 0.5;
  background: pink;
}

.services .items .item:hover .text {
  background: #ffffff;
}

.services .items .item:hover .text .h4 {
  color: #000000;
  font-weight: 800;
}

.services .items .item:hover .text .a {
  color: #000000;
  font-weight: 800;
}

.services .items .item .img {
  width: 863px;
  height: 500px;
  background: #000000;
  border: 1px solid #7ed321;
  transition: 1s;
}

.services .items .item .text {
  background: #7ed321;
  width: 26%;
  height: 500px;
  border: 1px solid #7ed321;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transition: 1s;
}

.services .items .item .text .h4 {
  text-align: left;
  width: 80%;
  transition: 1s;
  color: white;
}

.services .items .item .text .h6 {
  text-align: left;
  width: 80%;
  margin-top: 10%;
  margin-bottom: 15%;
}

.services .items .item .text .a {
  width: 80%;
  transition: 1s;
  color: white;
}

.slider {
  margin-bottom: 4.1%;
}

.slider .h1 {
  margin-top: 9.2%;
  margin-bottom: 4.3%;
}

.slider .carousel-inner h3 {
  background-color: rgba(20, 49, 68, 0.6);
  padding: 20px;
  animation: anime-H3 1.3s ease-out;
}

.slider .carousel-inner p {
  font-size: 18px;
  background-color: rgba(30, 29, 29, 0.6);
  padding: 20px;
  animation: anime-P 1.6s ease-out;
}

@keyframes anime-H3 {
  from {
    opacity: 0;
    transform: translateX(-2000px);
  }

  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes anime-P {
  from {
    opacity: 0;
    transform: translateX(2000px);
  }

  to {
    opacity: 1;
    transform: translateX(0);
  }
}

.useful {
  background: #7ed321;
  padding-bottom: 47px;
  margin-bottom: 88px;
}

.useful .h1 {
  text-align: left;
  color: #ffffff;
  font-weight: 300;
  padding-top: 5%;
  padding-bottom: 3%;
  padding-left: 2%;
}

.useful .content {
  display: flex;
  border-left: 2px solid white;
  padding-left: 2%;
}

.useful .content .text {
  width: 63%;
}

.useful .content .text .h5 {
  width: 90%;
  text-align: left;
  font-family: Helvetica, sans-serif;
}

.useful .content .img {
  width: 30%;
  border: 1px solid white;
  display: flex;
  justify-content: center;
  align-items: center;
}

.useful .content .img div {
  width: 80%;
  padding-top: 60%;
  background: black;
}

.footer .span {
  font-size: 36px;
  display: flex;
}

.footer .span .green {
  color: #7ed321;
  font-weight: bold;
}

.footer .content {
  display: flex;
